import React, { useState, useEffect } from 'react';
import Logo from '../../components/Logo/Logo';
import VideoBackground from '../../components/VideoBackground/VideoBackground';
import './Keystroke.css';
import 'animate.css';

const SECONDS = 88;

function Keystroke({ onSubmit }) {

  const [fadeOut, setFadeout] = useState(false);
  useEffect(() => {
    document.onkeydown = (e) => {
      if (e.keyCode === SECONDS) {
        setFadeout(true);
        setTimeout(onSubmit, 2000);
      }
    }
  }, []);

  const containerClasses = fadeOut ? 'animated slow fadeOut' : 'NSChristmas-';

  return (
    <div className={containerClasses}>
      <Logo />
      <VideoBackground />
      <div className="keystroke-container">
        Press christmas key combination with style to continue...
      </div>
    </div>
  );
}

export default Keystroke;
