import React, { useEffect, useState } from 'react';
import Logo from '../../components/Logo/Logo';
import Button from '../../components/Button/Button';
import VideoBackground from '../../components/VideoBackground/VideoBackground';
import 'animate.css';
import './Intro.css';

function getTime() {
    const second = 1000;
    const minute = second * 60;
    const hour = minute * 60;
    const day = hour * 24;

    const countDown = new Date('Dec 20, 2019 16:00:00').getTime();
    const now = new Date().getTime();
    const distance = countDown - now;
    return {
        days: Math.floor(distance / (day)),
        hours: Math.floor((distance % (day)) / (hour)),
        mins: Math.floor((distance % (hour)) / (minute)),
        secs: Math.floor((distance % (minute)) / second),
    }
}

function Intro({ onSubmit }) {
    const [countDown, setCountDown] = useState({ days: 0, hours: 0, mins: 0, secs: 0 });

    const { days, hours, mins, secs } = countDown;

    setInterval(() => {
        setCountDown(getTime());
    }, 1000);

    const handleClick = () => {
        // audio.pause();
        // audio.currentTime = 0;
        onSubmit();
    }

    useEffect(() => {
        const audio = document.createElement('audio');
        audio.src = 'https://srv-file7.gofile.io/download/g9T74O/MuOnline Devias Theme (1).mp3';
        audio.play();
    }, []);


    return (
        <div className="animated slow fadeIn">
            <Logo />
            <VideoBackground />
            <div className="intro-container">
                <div>
                    {/* <h1>Christmas special winners</h1>
                    <div className="winner-container">
                        <div>#1 Pijuskas Jonukas</div>
                        <div>#2 Pijuskas Jonukas</div>
                        <div>#3 Pijuskas Jonukas</div>
                    </div> */}
                    <div class="countdown-container">
                        <h1>HELPING REGULAR ELF</h1>
                        <h2>Christmas challenge ends in:</h2>
                        <ul>
                            <li><span id="days"></span>{days}days</li>
                            <li><span id="hours"></span>{hours}hours</li>
                            <li><span id="minutes"></span>{mins}minutes</li>
                            <li><span id="seconds"></span>{secs}seconds</li>
                        </ul>
                    </div>
                    <div>Look for clues. Answer will bring you to next stage. There are 4 stages. Good luck. Ho ho ho!</div>
                </div>
                <Button onClick={handleClick}>Start</Button>
            </div>
        </div>
    );
}

