import React, { useState } from 'react';
import Logo from '../../components/Logo/Logo';
import VideoBackground from '../../components/VideoBackground/VideoBackground';
import Keypad from '../../components/Keypad/Keypad';
import LightProjector from '../../components/LightProjector/LightProjector';
import 'animate.css';
import './keyboard.css';

const REPEAT = '476501';
let curr = 0;
// y: '-.--', z: '--..', 1: '.----', 2: '..---',
//   3: '...--', 4: '....-', 5: '.....', 6: '-....',
//     7: '--...', 8: '---..', 9: '----.', 0: '-----',
function Keyboard({ onSubmit }) {
  const handleSubmit = (a) => a === REPEAT ? onSubmit() : undefined;
  const [projectorOn, setProjectorOn] = useState(false);
  const message = [
    3000, 1000, 3000, 1000, 3000, 1000, 3000, 1000, 6000, 9000, // 4
    6000, 1000, 6000, 1000, 3000, 1000, 3000, 1000, 3000, 9000, // 7
    6000, 1000, 3000, 1000, 3000, 1000, 3000, 1000, 3000, 9000, // 6
    3000, 1000, 3000, 1000, 3000, 1000, 3000, 1000, 3000, 9000, // 5
    6000, 1000, 6000, 1000, 6000, 1000, 6000, 1000, 6000, 9000, // 0
    3000, 1000, 6000, 1000, 6000, 1000, 6000, 1000, 6000, 9000, // 1
  ];

  setTimeout(() => {
    setProjectorOn(!projectorOn);
    if (message.length <= curr + 1) {
      curr = 0;
    } else {
      curr = curr + 1;
    }
  }, message[curr])


  return (
    <div className="animated slow fadeIn">
      <Logo />
      <VideoBackground />
      <div className="keypad-container">
        <LightProjector on={projectorOn}>{message[curr]}</LightProjector>
        <Keypad onSubmit={handleSubmit} />
      </div>

    </div>
  );
}

export default Keyboard;
