import React from 'react';
import './videoBackground.css';

const DEFAULT = 'https://www.youtube.com/embed/2Ukni0UdafU?controls=0&showinfo=0&rel=0&autoplay=1&loop=1&playlist=2Ukni0UdafU';
const FIREWORKS = 'https://www.youtube.com/embed/0E4haJHYUJw?controls=0&showinfo=0&rel=0&autoplay=1&loop=1&playlist=2Ukni0UdafU';

function VideoBackground({ fireworks }) {
  return (
    <div class="video-background">
      <div class="video-foreground">
        <iframe
          title="bg"
          width="560"
          height="315"
          src={fireworks ? FIREWORKS : DEFAULT} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        />
      </div>
    </div>
  );
}

export default VideoBackground;
