import React, { useState } from 'react';
import Button from '../../components/Button/Button';
import 'animate.css';
import './letterFromSanta.css';
import Logo from '../../components/Logo/Logo';

const EFFECT = 'Twinkle';

function LetterFromSanta({ onSubmit }) {
    const [error, setError] = useState(false);
    const [value, setValue] = useState(null);

    const handleSeal = (e) => {
        if (value !== EFFECT) {
            setError(true);
            setTimeout(() => setError(false), 2000);
        } else {
            onSubmit();
        }
    }

    return (
        <div className="letter-root">

            <Logo />
            <div class="candle">
                <div class="flame">
                    <div class="shadows"></div>
                    <div class="top"></div>
                    <div class="middle"></div>
                    <div class="bottom"></div>
                </div>
                <div class="wick"></div>
                <div class="wax"></div>
            </div>
            <div className="LetterFromSanta-container animated slow fadeIn">
                I'm writing this message as a fail-safe, so if you're reading this now, it means you're going after the santa sleights... We went to run some field tests on rotation... Please seal this letter and deliver it to Santa. It's christmas time!  
                 <br /> Signed, <br />Senior chief snow elf <input onChange={(e) => setValue(e.target.value)} className="inpt" type="text" placeholder="afcd78b6b2510f160d70ac33a966d6df" />
                <Button disabled={error} onClick={handleSeal}>{
                    !error ? <span className="animated slow fadeIn">Seal</span> : <span className="animated slow fadeIn">Wrong signature</span>}</Button>

            </div>
        </div>
    );
}

export default LetterFromSanta;
