import React, { useEffect, useState } from 'react';
import './firePlace.css';
import 'animate.css';

function FirePlace({ onSubmit }) {
    const [visible, setVisible] = useState(false);

    const containerClass = visible ? 'FirePlace-container up-the-container' : 'FirePlace-container';
    const rootClass = !visible ? 'animated slow fadeIn' : 'animated slow fadeOut';
    useEffect(() => {
        window.onresize = (e) => {
            if (e.currentTarget.devicePixelRatio === 5) {
                setVisible(true);
                setTimeout(() => {
                    onSubmit();
                }, 2000);
            } else {
                setVisible(false);
            }
        };
    }, []);
    
    return (
        <div className={rootClass}>
            <div className="moon" />
            <div className="stars" />
            <div className="twinkling" />
            <div className="clouds" />
            <div className={containerClass}>
                {!visible ? 'This was always a moonshot... Look to the moon' : 'See! Easy...'}
            </div>
        </div>
    );
}

export default FirePlace;
