import React, { useState } from 'react';
import './keypad.css';

function Keypad({ onSubmit }) {
  const maxLength = 6;
  const [value, setValue] = useState('');

  const onKeyPress = (val) => {
    if (maxLength >= value.length) {
      if (value !== 'enter') {
        setValue(value.concat(val));
      }
    }
    
    if (val === 'enter') {
      if (typeof onSubmit === 'function') {
        onSubmit(value);
      }
      setValue('');
    }
  }

  return (
    <div className="keypad">
      <div className="dots">
        {new Array(maxLength).fill(0).map((_, i) => <div className={value.length > i ? 'active-value' : ''} />)}
      </div>
      <div className="content">
        <div onClick={() => onKeyPress('enter')} className="enter">Enter</div>
        <div onClick={() => onKeyPress(1)}>1</div>
        <div onClick={() => onKeyPress(2)}>2</div>
        <div onClick={() => onKeyPress(3)}>3</div>
        <div onClick={() => onKeyPress(4)}>4</div>
        <div onClick={() => onKeyPress(5)}>5</div>
        <div onClick={() => onKeyPress(6)} className="b6">6</div>
        <div onClick={() => onKeyPress(7)} className="b7">7</div>
        <div onClick={() => onKeyPress(8)} className="b8">8</div>
        <div onClick={() => onKeyPress(9)} className="b9">9</div>
        <div onClick={() => onKeyPress(0)} className="b0">0</div>
      </div>
    </div>
  );
}

export default Keypad;
