import React, { useState } from 'react';

import IntroStage from './stage/Intro/Intro';
import KeybardStage from './stage/Keyboard/Keyboard';
import KeystrokeStage from './stage/Keystroke/Keystroke';
import FirePlaceStage from './stage/FirePlace/FirePlace';
import LetterFromSantaStage from './stage/LetterFromSanta/LetterFromSanta';
import FinalWordsStage from './stage/FinalWords/FinalWords';

const Stages = [
  IntroStage,
  KeybardStage,
  KeystrokeStage,
  FirePlaceStage,
  LetterFromSantaStage,
  FinalWordsStage,
];


function App() {
  const [appStage, setAppStage] = useState(0);

  const nextStage = () => setAppStage(appStage + 1);
  const handleSubmit = () => nextStage();

  const Stage = Stages[appStage];

  return (
    <div>
      <Stage onSubmit={handleSubmit} />
    </div>
  );
}

export default App;
